import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper';
import 'swiper/css/swiper.css';
export default {
  name: "GalleryTwo",
  components: {
    Swiper,
    SwiperSlide
  },
  directives: {
    swiper: directive
  },
  mounted() {
    new GLightbox({
      selector: '.img-popup',
      touchNavigation: true,
      loop: true,
      autoplayVideos: true
    });
  },
  data() {
    return {
      swiperOptions: {
        slidesPerView : 4,
        loop: true,
        speed: 1000,
        spaceBetween : 30,
        autoplay: {
          delay: 3000,
          disableOnInteraction: false
        },
        pagination: {
          el: '.swiper-pagination',
          type: 'bullets',
          clickable: true
        },

        // Responsive breakpoints
        breakpoints: {
          1024:{
            slidesPerView : 4
          },
          768:{
            slidesPerView : 2
          },
          640:{
            slidesPerView : 2
          },
          320:{
            slidesPerView : 1
          }
        }
      },

    }
  },
}
